import React, { useState, useEffect, useCallback, useContext, useRef } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { motion, AnimatePresence } from "framer-motion";

import { usePesquisa } from "../context/PesquisaContext";
import { useCarrinho } from "../context/CarrinhoContext";
import { AuthContext } from "../context/AuthContext";

import "../css/global/partials/header.css";
import "../css/global/partials/headerBusca.css";
import "../css/global/partials/itensPesquisa.css";

const Header = ({ tipo }) => {
  const navigate = useNavigate();
  const { authToken } = useContext(AuthContext);
  const [menuAberto, setMenuAberto] = useState(false);
  const [menuCarrinhoAberto, setMenuCarrinhoAberto] = useState(false);
  const [mostrarHeader, setMostrarHeader] = useState(true);

  const [produtos, setProdutos] = useState([]);
  const [ordenacao] = useState("relevancia");

  const location = useLocation();
  const { termoPesquisa, setTermoPesquisa } = usePesquisa();
  const { carrinho, alterarQuantidade, removerDoCarrinho } = useCarrinho();

  const carrinhoRef = useRef(null);

  const fecharMenu = useCallback(() => setMenuAberto(false), []);

  const fecharMenuCarrinho = useCallback(() => {
    setMenuCarrinhoAberto(false);
    fecharMenu();
  }, [fecharMenu]);

  const alternarMenu = () => {
    setMenuAberto((prev) => {
      const novoEstado = !prev;
      if (novoEstado) {
        setTermoPesquisa("");
      }
      return novoEstado;
    });

    if (menuCarrinhoAberto) {
      setMenuCarrinhoAberto(false);
    }
  };

  const alternarMenuCarrinho = () => {
    setMenuCarrinhoAberto((prev) => {
      if (!prev && menuAberto) {
        setMenuAberto(false);
      }
      return !prev;
    });
  };


  const produtosFiltrados = produtos.filter((produto) =>
    produto.nome.toLowerCase().startsWith(termoPesquisa.toLowerCase())
  );

  const usarIncludes =
    termoPesquisa &&
    produtosFiltrados.length === 0 &&
    produtos.filter((produto) =>
      produto.nome.toLowerCase().includes(termoPesquisa.toLowerCase())
    );

  const resultadosPesquisa = produtosFiltrados.length > 0
    ? produtosFiltrados
    : usarIncludes || [];

  const produtosOrdenados = [...resultadosPesquisa].sort((a, b) => {
    if (ordenacao === "preco-asc") {
      return a.preco - b.preco;
    } else if (ordenacao === "preco-desc") {
      return b.preco - a.preco;
    } else if (ordenacao === "relevancia") {
      return 0.5 - Math.random();
    }
    return 0;
  });

  useEffect(() => {
    fetch('https://ironfit-backend.onrender.com/produtos')
      .then(res => res.json())
      .then(data => setProdutos(data));
  }, []);

  useEffect(() => {
    setTermoPesquisa("");
  }, [location.pathname, setTermoPesquisa]);

  useEffect(() => {
    setMostrarHeader(true);

    const timeout = setTimeout(() => {
      let prevScrollPos = window.pageYOffset;

      const handleScroll = () => {
        const currentScrollPos = window.pageYOffset;

        setMostrarHeader(prevScrollPos > currentScrollPos || currentScrollPos < 10);
        prevScrollPos = currentScrollPos;

        if (menuAberto) fecharMenu();
      };

      window.addEventListener("scroll", handleScroll);
      return () => window.removeEventListener("scroll", handleScroll);
    }, 300);

    return () => clearTimeout(timeout);
  }, [location, menuAberto, fecharMenu]);

  useEffect(() => {
    function handleClickForaCarrinho(event) {
      if (
        menuCarrinhoAberto &&
        carrinhoRef.current &&
        !carrinhoRef.current.contains(event.target)
      ) {
        setMenuCarrinhoAberto(false);
      }
    }

    document.addEventListener("mousedown", handleClickForaCarrinho);

    return () => {
      document.removeEventListener("mousedown", handleClickForaCarrinho);
    };
  }, [menuCarrinhoAberto]);

  const quantidadeItensCarrinho = carrinho.length;

  const precoTotal = carrinho.reduce((total, produto) => total + produto.preco * produto.quantidade, 0).toFixed(2);

  return (
    <header className={`cabecalho ${mostrarHeader ? "visivel" : "escondido"}`}>
      <nav className={`cabecalho_nav ${menuAberto ? "ativo" : ""}`}>
        <div className="div_titulo_academia">
          <Link to="/">
            <h1 className="titulo_academia">
              Iron <b className="titulo_academia_destaque">Fit</b>
            </h1>
          </Link>
        </div>

        <div className="links_1024">
          <Link to="/todos_produtos">
            <p className="item_menu_hamburguer link_1024">Nossos Produtos</p>
          </Link>
          <Link to="/#planos" onClick={fecharMenu}>
            <p className="item_menu_hamburguer link_1024">Planos</p>
          </Link>
          {authToken ? (
            <Link to="/perfil">
              <p className="item_menu_hamburguer link_1024 link_1280">Perfil</p>
            </Link>
          ) : (
            <Link to="/cadastro">
              <p className="item_menu_hamburguer link_1024 link_1280">Sua Conta</p>
            </Link>
          )}
        </div>

        {(location.pathname.startsWith("/todos_produtos") || location.pathname.startsWith("/produto/") || location.pathname.startsWith("/checkout")) && (
          <div className="campo-pesquisa">
            <input
              type="text"
              id="pesquisa"
              maxLength={85}
              placeholder="Encontre o que procura aqui..."
              className="input-pesquisa"
              value={termoPesquisa}
              onChange={(e) => {
                const termo = e.target.value;
                setTermoPesquisa(
                  termo.charAt(0).toUpperCase() + termo.slice(1).toLowerCase()
                );
                fecharMenuCarrinho();
              }}
              onKeyDown={(e) => {
                if (e.key === "Enter") {
                  e.preventDefault();
                  navigate("/todos_produtos");
                }
              }}
            />
          </div>
        )}

        {termoPesquisa && resultadosPesquisa.length > 0 && (
          <div className="destaques-home">
            <h2 className="titulo_pesquisa">Resultado da Pesquisa:</h2>
            <div className="produtos-grid">
              {produtosOrdenados.slice(0, 3).map((produto) => (
                <div className="produto-card">
                  <Link
                    to={`/produto/${produto.idproduto}`}
                    key={produto.idproduto}
                    onClick={() => setTermoPesquisa("")}
                  >
                    <img className="img-pesquisado" src={produto.imagens} alt={produto.nome} />
                  </Link>
                  <div className="infos_pesquisa">
                    <Link
                      to={`/produto/${produto.idproduto}`}
                      key={produto.idproduto}
                      onClick={() => setTermoPesquisa("")}
                    >
                      <h3>{produto.nome}</h3>
                      <p>R${produto.preco}</p>
                    </Link>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}

        <button
          className={`cabecalho_nav_menu_hamburguer ${menuCarrinhoAberto ? "ativo" : ""}`}
          onClick={alternarMenu}
          aria-label="Menu"
        >
          <div className={`linhas_hamburguer ${menuAberto ? "ativo" : ""}`}>
            <div className="botao_hamburguer linha1"></div>
            <div className="botao_hamburguer linha2"></div>
            <div className="botao_hamburguer linha3"></div>
          </div>
          {quantidadeItensCarrinho > 0 && !menuCarrinhoAberto && !menuAberto && (
            <motion.div
              className="bolinha"
              initial={{ scale: 0, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0, opacity: 0 }}
              transition={{ duration: 0.2, ease: "easeOut" }}
            >
              <AnimatePresence mode="wait">
                <motion.span
                  key={quantidadeItensCarrinho}
                  initial={{ rotateX: 90, opacity: 0 }}
                  animate={{ rotateX: 0, opacity: 1 }}
                  exit={{ rotateX: -90, opacity: 0 }}
                  transition={{ duration: 0.2, ease: "easeOut" }}
                >
                  {quantidadeItensCarrinho}
                </motion.span>
              </AnimatePresence>
            </motion.div>
          )}
        </button>

        <ul className={`nav-lista ${menuAberto ? "ativo" : ""}`}>
          <li className="nav-lista_item">
            <Link to="/#planos" onClick={fecharMenu}>
              <p className="item_menu_hamburguer menor_1024">Planos</p>
            </Link>
          </li>

          {authToken ? (
            <>
              <li className="nav-lista_item">
                <Link to="/perfil" onClick={fecharMenu}>
                  <p className="item_menu_hamburguer menor_1280">Perfil</p>
                </Link>
              </li>
            </>
          ) : (
            <li className="nav-lista_item">
              <Link to="/cadastro" onClick={fecharMenu}>
                <p className="item_menu_hamburguer menor_1280">Sua Conta</p>
              </Link>
            </li>
          )}

          <li className="nav-lista_item">
            <button onClick={alternarMenuCarrinho} className="item_menu_hamburguer botao_carrinho_menu">
              Seu Carrinho
              {quantidadeItensCarrinho > 0 && (
                <span className="bolinha-carrinho">{quantidadeItensCarrinho}</span>
              )}
            </button>
          </li>

          <li className="nav-lista_item">
            <Link to="/todos_produtos">
              <p className="item_menu_hamburguer menor_1024">Nossos Produtos</p>
            </Link>
          </li>

          <li className="nav-lista_item">
            <Link to="/horarios" onClick={fecharMenu}>
              <p className="item_menu_hamburguer">Horrios</p>
            </Link>
          </li>
        </ul>
      </nav>

      <div ref={carrinhoRef} className={`menu-carrinho ${menuCarrinhoAberto ? "ativo" : ""}`}>
        <button className="cabecalho_nav_menu_hamburguer" onClick={fecharMenuCarrinho} aria-label="Menu">
          <div className={`linhas_hamburguer ${menuAberto || menuCarrinhoAberto ? "ativo" : ""}`}>
            <div className="botao_hamburguer linha1"></div>
            <div className="botao_hamburguer linha2"></div>
            <div className="botao_hamburguer linha3"></div>
          </div>
        </button>

        <h2 className="titulo_carrinho">Seu <b>Carrinho</b></h2>
        {carrinho.length === 0 ? (
          <p className="carrinho_vazio">Seu carrinho est vazio.</p>
        ) : (
          <ul className="lista_produtos">
            {carrinho.map((produto, index) => (
              <li className="item_produtos" key={index}>

                <div className="img_desc_carrinho">
                  <Link to={`/produto/${produto.idproduto}`} className="produto-link" onClick={fecharMenuCarrinho}>
                    <img className="img_lista_carrinho" src={produto.imagens} alt={produto.nome} width="50" />
                  </Link>

                  <div className="descricao_produtos_lista">
                    <div className="descricao_carrinho">
                      <Link to={`/produto/${produto.idproduto}`} className="produto-link" onClick={fecharMenuCarrinho}>
                        <span className="nome_carrinho">{produto.nome}</span>
                      </Link>

                      <div className="ajuste_botao">
                        <Link to={`/produto/${produto.idproduto}`} className="produto-link" onClick={fecharMenuCarrinho}>
                          <span>
                            Preo: R$
                            <motion.span
                              key={produto.preco * produto.quantidade}
                              initial={{ opacity: 0, y: -5 }}
                              animate={{ opacity: 1, y: 0 }}
                              exit={{ opacity: 0, y: 5 }}
                              transition={{ duration: 0.3, ease: "easeOut" }}
                            >
                              {(produto.preco * produto.quantidade).toFixed(2)}
                            </motion.span>
                          </span>
                        </Link>
                        <button className="botao_remover" onClick={() => removerDoCarrinho(produto.idproduto)}>&#x02A2F;</button>
                      </div>
                    </div>

                    <div className="div_quantidade">
                      <button
                        className="botoes_carrinho" onClick={() => alterarQuantidade(produto.idproduto, -1)}>
                        -
                      </button>

                      <span className="span_quantidade">{produto.quantidade}</span>

                      <button className="botoes_carrinho"
                        onClick={() => alterarQuantidade(produto.idproduto, 1)}
                        disabled={produto.quantidade >= 5}>
                        +
                      </button>
                    </div>
                  </div>
                </div>
              </li>
            ))}
          </ul>
        )}

        {carrinho.length > 0 && (
          <div className="total-compra">
            <p className="preco_total">
              Preo Total: R$
              <motion.span
                key={precoTotal}
                initial={{ opacity: 0, y: -10 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: 10 }}
                transition={{ duration: 0.3, ease: "easeOut" }}
              >
                {precoTotal}
              </motion.span>
            </p>
            <Link to="/checkout" className="continuar-compra-link">
              Continuar Compra
            </Link>
          </div>
        )}
      </div>
    </header>
  );
};

export default Header;